.back-top {
	position: fixed;
	right: 0;
	width: 5em;
	text-align: center;
	opacity: 0;
	transition: $transition--all;
	i.fa{
		font-size: 3rem;
	}
	span {
		display: block;
		font-size: 1.2rem;
		&::selection {
			background-color: transparent;
		}
	}
	&.back-top--scrolled {
		bottom: 5vh;
		cursor: pointer;
		opacity: .5;
		transition: $transition--all;
		&:hover {
			opacity: 1;
		}
	}
}


