.clock__wrapper{
    width: 100%;
    margin: auto;
    text-align: center;
    min-height: 16rem;
}
.clock__segment {
    display: inline-block;
    padding: 2rem;
}
.segment__num {
    background-color: $nacac_translucent-blue;
    padding: 0;
    border-radius: 50%;
    width: 8rem;
    height: 8rem;
    text-align: center;
    font-size: 4rem;
    line-height: 8.4rem;
    font-family: $sans;
    margin-bottom: 2rem;
}

.segment__label{
	@extend .hero__text;
	text-transform: capitalize;
	font-weight: bold;
}

